<template>
    <div id="portfolio" class="shadow py-8">
        <div class="p-tab pb-4 pl-8">
            <ul class="flex gap-2 overflow-auto">
                <li @click="filterTag = 'All', showAllTag = true" :class="{bg_yellow_400 : filterTag === 'All'}">All</li>
                <li @click="filterTag = 'PHP', showAllTag = false" :class="{bg_yellow_400 : filterTag === 'PHP'}">PHP</li>
                <li @click="filterTag = 'Laravel', showAllTag = false" :class="{bg_yellow_400 : filterTag === 'Laravel'}">Laravel</li>
                <li @click="filterTag = 'VueJs', showAllTag = false" :class="{bg_yellow_400 : filterTag === 'VueJs'}">VueJs</li>
                <li @click="filterTag = 'NodeJs', showAllTag = false" :class="{bg_yellow_400 : filterTag === 'NodeJs'}">NodeJs</li>
                <li @click="filterTag = 'MySQL', showAllTag = false" :class="{bg_yellow_400 : filterTag === 'MySQL'}">MySQL</li>
                <li @click="filterTag = 'MongoDB', showAllTag = false" :class="{bg_yellow_400 : filterTag === 'MongoDB'}">MongoDB</li>
                <li @click="filterTag = 'Games', showAllTag = false" :class="{bg_yellow_400 : filterTag === 'Games'}">Games</li>
            </ul>
        </div>

        <div class="px-8">
            <h1 class="text-gray-700">Projects</h1>
            <div class="py-3">
                <div class="border-t border-yellow-500 w-1/4 absolute"></div>
                <div class="border-t border-gray-300 w-full "></div>
            </div>
        </div>

        <div class="grid sm:grid-cols-3 md:grid-cols-4 py-2 justify-items-center gap-y-10">
            <div v-for="(project, index) in porjects" :key="index">
                <div class="card rounded shadow border-t-4 border-b-4 border-yellow-300 hover:shadow-xl">
                    <div class="card-header grid justify-items-center items-center bg-gradient-to-r from-yellow-100 to-gray-200 w-36 h-36">
                        <h1 class="text-center">{{project.name}}</h1>
                    </div>
                    <div class="card-body py-2 h-28">
                        <!-- <h1>{{project.name}}</h1> -->
                        <div v-for="tag in project.tag" :key="tag">
                            <p>{{tag}}</p>
                        </div>
                    </div>
                    <div class="card-footer py-2 w-full text-center">
                        <a :href="project.link" target="blank">View</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Swal from 'sweetalert2'

export default {
    data() {
        return {
            filterTag: 'All',
            showAllTag: true,
            projects: [
                {'name': 'Book Store', 'tag': ['Laravel','PHP', 'JavaScript', 'Jquery', 'Ajax', 'Bootstrap', 'MySQL'], 'link': 'https://github.com/NakulDeb/Online-Book-Store'},
                {'name': 'nakuldeb', 'tag': ['VueJs', 'JavaScript', 'TailwindCss', 'Google API'], 'link': 'https://nakuldeb.me'},
                {'name': 'Class Routine RESTful API', 'tag': ['NodeJs', 'JavaScript', 'xpress', 'Mongos', 'MongoDB'], 'link': 'https://github.com/NakulDeb/Software-Development-Lab-III---Class-Routine-RESTful-API'},
                {'name': 'Blog Post', 'tag': ['PHP', 'JavaScript', 'Jquery', 'Ajax', 'Bootstrap', 'MySQL'], 'link': '#'},
                {'name': 'Bulk Ly', 'tag': ['Laravel', 'PHP', 'JavaScript', 'Jquery', 'Ajax', 'Bootstrap', 'MySQL'], 'link': 'https://bulk.ly'},
                {'name': 'Constructing Puzzle Game 3D', 'tag': ['Unity 3D', 'C#', 'Games', 'Blander'], 'link': 'https://github.com/NakulDeb/Constructing-Puzzle-Game-3D'},
                {'name': 'Constructing Puzzle Game with VR', 'tag': ['Unity 3D', 'C#', 'Steam VR', 'Games', 'Blander'], 'link': 'https://github.com/NakulDeb/Constructing-Puzzle-Game-3D'},
                {'name': 'Flappy Bird 2D Game', 'tag': ['Unity 3D', 'C#', 'Games'], 'link': 'https://github.com/NakulDeb/Flappy-Bird-2D-Game'},
                {'name': 'Microprocessor And Assembly', 'tag': ['Microprocessor', 'Assembly', '8086 Architecture', '8086 Emulator'], 'link': 'https://github.com/NakulDeb/CSE3232-Microprocessor-And-Assembly-Language-Lab'},
                {'name': 'Digital Image Processing', 'tag': ['Matlab', 'Image Processing'], 'link': 'https://github.com/NakulDeb/CSE4182-Digital-Image-Processing-Lab'},
                {'name': 'Web Scraping with Python', 'tag': ['Python', 'Scraping', 'Beautifulsoup 4', 'Selenium'], 'link': 'https://github.com/NakulDeb/Web-Scraping-with-Python'},
                {'name': '-----', 'tag': ['NodeJs', 'JavaScript', 'Jquery', 'Ajax', 'Bootstrap', 'Html', 'Css'], 'link': 'https://nakuldeb.me'},
            ]
        }
    },
    computed: {
        porjects() {
            if (this.filterTag === 'All'){
                return this.projects;
            }
            var $this = this

            // return $this.projects.filter(function (project) {
            //     return project.tech === $this.filterTag
            // })
            return $this.projects.filter(function (project) {
                return project.tag.some(function(tag){
                    return tag === $this.filterTag
                })
            })
        }
    },
    mounted() {
        
    },
    methods: {

    },
    
}
</script>
<style>
    @import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,300&family=Source+Sans+Pro:wght@300&family=Imbue:wght@400&family=Dancing+Script:wght@600&display=swap');
    #portfolio {
        font-family: 'Merriweather', serif;
    }
    .text-xsm {
        font-size: 0.8rem;
        font-family: 'open Sans', Arial;
    }
    .hover-effect {
        @apply hover:bg-gray-900 hover:shadow transform duration-200 hover:scale-110
    }
    .p-tab>ul>li {
        @apply px-2 border rounded text-xsm text-gray-50 bg-gray-600 cursor-pointer hover-effect focus:bg-yellow-600
    }
    .card-footer>a {
        @apply px-2 py-1 text-xsm rounded text-gray-50 font-semibold bg-purple-600 hover:bg-purple-700
    }
    .card {
        @apply transform duration-200 hover:scale-105
    }
    .bg_yellow_400 {
        @apply bg-yellow-400 !important
    }
    .card-header>h1 {
        @apply text-lg text-yellow-500 font-extrabold;
        font-family: 'open Sans', Arial
    }
    .card-body>h1 {
        font-family: 'open Sans', Arial;
        @apply text-sm font-semibold w-36 pb-3
    }
    .card-body>div {
        font-family: 'open Sans', Arial;
        font-size: 12px;
        line-height: 1rem;
        @apply text-center w-36 px-3
    }

</style>