<template>
    <div id="contact" class="shadow">
        <div class="grid sm:grid-cols-2 md:grid-cols-5 py-8 justify-items-center">
            <div class="pb-8 md:col-span-2 w-full px-3 md:px-5 lg:px-8">
                <h1 class="text-gray-700">Contact info</h1>
                <div class="py-3">
                    <div class="border-t border-yellow-500 w-8 absolute"></div>
                    <div class="border-t border-gray-300 w-full "></div>
                </div>
                <div class="flex">
                    <div class="">
                        <div class="border-l border-yellow-500 h-8 absolute"></div>
                        <div class="border-l border-gray-300 h-full "></div>
                    </div>
                    <div class="pl-3 text-xsm">
                        <ul>
                            <li class="pb-3">Mohammadpur Dhaka 1207</li>
                            <li>Email: nakuldeb.cse.ru@gamil.com</li>
                            <li>Phone: +880177-0826205</li>
                            <li>www.nakuldeb.me</li>
                        </ul>
                    </div>
                </div>
                <div class="message-anim h-1/2"> 
                </div>
            </div>
            <div class="md:col-span-3 w-full px-3 md:px-5 lg:px-8">
                <h1 class="text-gray-700">Message</h1>
                <div class="py-3">
                    <div class="border-t border-yellow-500 w-8 absolute"></div>
                    <div class="border-t border-gray-300 w-full "></div>
                </div>
                <div class="pl-3 text-xsm">
                    <form ref="formElement" @submit.prevent="sendMessage" id="message-form" class="text-sm">
                        <input type="hidden" name="entry.1140221610" v-model="ip">
                        <fieldset>
                            <legend class="px-1">Your Full Name</legend>
                            <input name="entry.1811125599" class="w-full focus:outline-none" type="text" required>
                        </fieldset>

                        <fieldset>
                            <legend class="px-1">Your Email</legend>
                            <input name="entry.599195461" class="w-full focus:outline-none" type="email" required>
                        </fieldset>

                        <fieldset>
                            <legend class="px-1">Message</legend>
                            <textarea name="entry.2015874026" class="w-full focus:outline-none" cols="30" rows="5" required></textarea>
                        </fieldset>

                        <button class="p-2 w-1/2 sm:w-full md:w-1/2 rounded text-gray-50 font-semibold bg-purple-600 hover:bg-purple-700">
                            <div class="flex justify-center">
                                <span>Send Message</span>
                            <img class="ml-2" style="display:none" :src="require('/src/assets/img/lodding_icon.png')" alt="" width="20">
                            </div>
                        </button>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Swal from 'sweetalert2'

export default {
    data() {
        return {
            ip: '',
        }
    },
    mounted() {
        this.getIp();
    },
    methods: {
        async getIp(){
            const response = await axios.get('https://api.ipify.org/?format=json')
            this.ip = response.data.ip
        },
        sendMessage(){
            var $this = this
            var formElement = $this.$refs.formElement
            var formData = new FormData(formElement)
            var url = "https://docs.google.com/forms/u/0/d/e/1FAIpQLSeX4B4ovkNVDHjS879-PlI5Xq6v1n1r17S97Ckz1jh5STquog/formResponse"
  
            axios.post(url, formData)

            formElement.reset()
            this.showToast()            
        },
        showToast() {
            const Toast = Swal.mixin({
                toast: true,
                position: 'top-end',
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                    toast.addEventListener('mouseenter', Swal.stopTimer)
                    toast.addEventListener('mouseleave', Swal.resumeTimer)
                }
            })
            Toast.fire({
                icon: 'success',
                title: 'Sant Message successful'
            })
        }
    },
    
}
</script>
<style>
    @import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,300&family=Source+Sans+Pro:wght@300&family=Imbue:wght@400&family=Dancing+Script:wght@600&display=swap');
    #contact {
        font-family: 'Merriweather', serif;
    }
    .text-xsm {
        font-size: 0.8rem;
        font-family: 'open Sans', Arial;
        @apply text-gray-700
    }
    #message-form>fieldset {
        font-family: 'Merriweather', serif;
        @apply p-2 my-5 border-2 rounded border-purple-400 hover:border-purple-600 hover:shadow
    }
    #message-from>fieldset>legend {
        @apply font-semibold text-gray-600
    }
    #message-from>fieldset>input {
        @apply w-full focus:outline-none
    }
    .message-anim {
        background: url('~@/assets/img/message.jpg')  center no-repeat;
        background-size: 100%;
    }
    @media (max-width: 640px) {
        .message-anim {
            display: none;
        }
    }
</style>